import React, { useEffect, useState } from 'react';
import { useSelector, useDispatch } from 'react-redux';
import { selectVal, handleChange } from './editorSlice';
import styles from './Editor.module.css';
import marked from 'marked';
import hljs from 'highlight.js/lib/core';
import javascript from 'highlight.js/lib/languages/javascript';
import 'highlight.js/styles/github.css';
import DOMPurify from 'dompurify';
hljs.registerLanguage('javascript', javascript);


export const Editor = () => {
  const val = useSelector(selectVal);
  const dispatch = useDispatch();
  const changeHandler = event => {
    dispatch(handleChange(event.target.value));
  }

  marked.setOptions({
    highlight: function(code, language) {
      const validLanguage = hljs.getLanguage(language) ? language : 'javascript';
      return hljs.highlight(validLanguage, code).value;
    },
    gfm: true,
    breaks: true,
    xhtml: true
  });

  const markedVal = (val) => {
    return (marked(val));
  }

  const sanitizer = DOMPurify.sanitize;

  return (
    <div className={styles.editor_wrapper}>
      <div className={styles.preview} id="preview" dangerouslySetInnerHTML={{__html: sanitizer(markedVal(val))}}></div>
      <textarea className={styles.textfield} id="editor" value={val} onChange={changeHandler}></textarea>
    </div>
  );
}
