import { createSlice } from '@reduxjs/toolkit';

export const editorSlice = createSlice({
  name: 'editor',
  initialState: {
    value:
    '# HTML Markdown Preview Sandbox\n\n## This is a sub-heading...\n### And here\'s some other cool stuff:\n\nHeres some code, `<div></div>`, between 2 backticks.\n\n```\n// this is multi-line code:\n\nfunction anotherExample(firstLine, lastLine) {\n  if (firstLine == \'```\' \&\& lastLine == \'```\') {\n    return multiLineCode;\n  }\n}\n```\n\nYou can also make text **bold**... whoa!\nOr _italic_.\nOr... wait for it... **_both!_**\nAnd feel free to go crazy ~~crossing stuff out~~.\n\nThere\'s also [links](https://www.freecodecamp.com), and\n> Block Quotes!\n\nAnd if you want to get really crazy, even tables:\n\nWild Header | Crazy Header | Another Header?\n------------ | ------------- | -------------\nYour content can | be here, and it | can be here....\nAnd here. | Okay. | I think we get it.\n\n- And of course there are lists.\n  - Some are bulleted.\n     - With different indentation levels.\n        - That look like this.\n\n\n1. And there are numbered lists too.\n1. Use just 1s if you want!\n1. And last but not least, let\'s not forget embedded images:\n\n![https://www.are.na/block/6058397](https://d2w9rnfcy7mm78.cloudfront.net/6058397/large_ce6ca279408e2b773ed72bda7f6cf097.jpg)\n',
  },
  reducers: {
    handleChange: (state, action) => {
      state.value = action.payload;
    }
  },
});

export const {  handleChange  } = editorSlice.actions;

export const selectVal = state => state.editor.value;

export default editorSlice.reducer;
